"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { collection, getDocs, addDoc, updateDoc, doc } from "firebase/firestore";
import { db } from "@/lib/firebase";

interface Product {
  id: string;
  codigo: string;
  nome: string;
  marca: string;
  aplicacao: string;
  dadosTecnicos: string;
  estoque: number;
  estoqueMinimo: number;
  status: "ativo" | "inativo";
}

export default function ProdutosPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [newProduct, setNewProduct] = useState({
    codigo: "",
    nome: "",
    marca: "",
    aplicacao: "",
    dadosTecnicos: "",
    estoque: 0,
    estoqueMinimo: 0,
  });
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isStockDialogOpen, setIsStockDialogOpen] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [quantityToAdd, setQuantityToAdd] = useState(0);

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const productsSnap = await getDocs(collection(db, "Produtos"));
        const productsData = productsSnap.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        })) as Product[];
        setProducts(productsData);
      } catch (error) {
        console.error("Error fetching products:", error);
      }
    };

    fetchProducts();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const docRef = await addDoc(collection(db, "Produtos"), {
        ...newProduct,
        status: "ativo",
      });

      setProducts([...products, {
        id: docRef.id,
        ...newProduct,
        status: "ativo",
      }]);

      setNewProduct({
        codigo: "",
        nome: "",
        marca: "",
        aplicacao: "",
        dadosTecnicos: "",
        estoque: 0,
        estoqueMinimo: 0,
      });
      setIsDialogOpen(false);
    } catch (error) {
      console.error("Error adding product:", error);
    }
  };

  const handleAddStock = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedProduct) return;

    try {
      const newStock = selectedProduct.estoque + quantityToAdd;
      await updateDoc(doc(db, "Produtos", selectedProduct.id), {
        estoque: newStock,
      });

      setProducts(products.map(product => 
        product.id === selectedProduct.id 
          ? { ...product, estoque: newStock }
          : product
      ));

      setSelectedProduct(null);
      setQuantityToAdd(0);
      setIsStockDialogOpen(false);
    } catch (error) {
      console.error("Error updating stock:", error);
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Produtos</h1>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button className="bg-black text-white hover:bg-gray-800">
              + Novo Produto
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Novo Produto</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Input
                  placeholder="Código"
                  value={newProduct.codigo}
                  onChange={(e) => setNewProduct({
                    ...newProduct,
                    codigo: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Nome"
                  value={newProduct.nome}
                  onChange={(e) => setNewProduct({
                    ...newProduct,
                    nome: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Marca"
                  value={newProduct.marca}
                  onChange={(e) => setNewProduct({
                    ...newProduct,
                    marca: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Aplicação"
                  value={newProduct.aplicacao}
                  onChange={(e) => setNewProduct({
                    ...newProduct,
                    aplicacao: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Textarea
                  placeholder="Dados Técnicos"
                  value={newProduct.dadosTecnicos}
                  onChange={(e) => setNewProduct({
                    ...newProduct,
                    dadosTecnicos: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  type="number"
                  placeholder="Estoque Mínimo"
                  value={newProduct.estoqueMinimo}
                  onChange={(e) => setNewProduct({
                    ...newProduct,
                    estoqueMinimo: parseInt(e.target.value),
                  })}
                  required
                />
              </div>
              <Button type="submit" className="w-full bg-black text-white hover:bg-gray-800">
                Salvar
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid grid-cols-2 gap-6">
        {products.map((product) => (
          <div
            key={product.id}
            className="bg-white rounded-lg shadow p-6"
          >
            <div className="flex justify-between items-start">
              <div>
                <div className="flex items-center space-x-4 mb-2">
                  <h3 className="font-medium">{product.nome}</h3>
                  <span className="text-sm text-gray-500">Código: {product.codigo}</span>
                </div>
                <p className="text-sm text-gray-500">
                  Marca: {product.marca}
                </p>
                <p className="text-sm text-gray-500">
                  Estoque: {product.estoque} unidades
                </p>
                <p className="text-sm text-gray-500">
                  Estoque Mínimo: {product.estoqueMinimo} unidades
                </p>
                {product.estoque <= product.estoqueMinimo && (
                  <span className="inline-block mt-2 px-3 py-1 rounded-full text-sm bg-red-100 text-red-800">
                    Estoque Baixo
                  </span>
                )}
              </div>

              <div className="flex space-x-2">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => {
                    setSelectedProduct(product);
                    setIsStockDialogOpen(true);
                  }}
                >
                  + Estoque
                </Button>
                <Button variant="ghost" size="sm">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="20"
                    height="20"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  >
                    <path d="M17 3a2.85 2.83 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3Z" />
                  </svg>
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>

      <Dialog open={isStockDialogOpen} onOpenChange={setIsStockDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Adicionar Estoque</DialogTitle>
          </DialogHeader>
          <form onSubmit={handleAddStock} className="space-y-4">
            <div>
              <Input
                type="number"
                placeholder="Quantidade"
                value={quantityToAdd}
                onChange={(e) => setQuantityToAdd(parseInt(e.target.value))}
                required
                min="1"
              />
            </div>
            <Button type="submit" className="w-full bg-black text-white hover:bg-gray-800">
              Adicionar
            </Button>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
